<template>
  <el-form
    :class="['form-flex-container', cunstomFormClass]"
    ref="portalForm"
    :model="formData"
    :rules="formRules"
    :label-position="labelPosition"
    :size="size"
    :style="formLineStyle"
    v-bind="$attrs"
    v-on="$listeners"
  >
    <el-row :gutter="rowGutter" type="flex" class="row-flex-wrap">
      <template v-for="(item, index) in formConfigList">
        <el-col
          v-if="!item.hidden"
          :key="index"
          :xs="colSmall * (item.smallSpan || 1)"
          :sm="colSmall * (item.smallSpan || 1)"
          :md="colSmall * (item.smallSpan || 1)"
          :lg="colSmall * (item.smallSpan || 1)"
          :xl="colXL * (item.bigSpan || 1)"
        >
          <!-- 表单项支持通过自定义插槽配置 -->
          <slot v-if="item.slotName" :name="item.slotName"></slot>
          <!-- 一个表单项中只有单个组件 -->
          <template v-else-if="typeof item.comp === 'string'">
            <el-form-item
              :label="item.label || ''"
              :prop="item.prop || item.key || ''"
              :class="item.formItemClass || ''"
            >
              <!-- 表单自定义label -->
              <template v-slot:label>
                <div class="custom-label" v-if="item.labelClear">
                  <span class="label-text">{{ item.label || '' }}</span>
                  <el-button
                    class="label-clear"
                    type="text"
                    size="small"
                    v-bind="item.labelClear.bind || {}"
                    v-on="item.labelClear.event || {}"
                    :style="item.labelClear.style"
                    :class="item.labelClear.class"
                  >
                    Clear
                  </el-button>
                </div>
              </template>
              <component
                :is="item.comp"
                :key="item.comp + item.key"
                :ref="item.ref || ''"
                :size="item.size || size"
                :style="item.style"
                :class="item.class"
                v-model="formData[item.key]"
                v-bind="item.bind"
                v-on="item.event"
                @keyup.enter.native="handleCommonEnterEvent(item.comp, item.key, formData[item.key])"
                @blur="handleCommonBlurEvent(item.comp, item.key, formData[item.key])"
              >
                <!-- 组件内的插槽 -->
                <template v-if="item.comp === 'el-select'">
                  <el-option
                    v-for="(option, idx) in item.options"
                    :key="idx + '' + option.value"
                    :label="option.label"
                    :value="option.value"
                  >
                  </el-option>
                </template>
                <template v-if="item.slots && item.slots.length">
                  <component
                    v-for="(slot, slotIdx) in item.slots"
                    :key="slot.name + slot.comp + slotIdx"
                    :is="slot.comp"
                    :slot="slot.name"
                    :class="slot.class"
                    :style="slot.style"
                    v-bind="slot.bind"
                    v-on="slot.event"
                  >
                  </component>
                </template>
              </component>
            </el-form-item>
          </template>
          <!-- 一个表单项中如果有多个组件 -->
          <template v-else-if="Object.prototype.toString.call(item.comp).slice(8, -1) === 'Array'">
            <!-- 组件在一个formItem中 -->
            <el-form-item
              v-if="item.sameGroup"
              :label="item.label || ''"
              :prop="item.prop || ''"
              :class="item.formItemClass || ''"
            >
              <!-- 表单自定义label -->
              <template v-slot:label>
                <div class="custom-label" v-if="item.labelClear">
                  <span class="label-text">{{ item.label || '' }}</span>
                  <el-button
                    class="label-clear"
                    type="text"
                    size="small"
                    v-bind="item.labelClear.bind || {}"
                    v-on="item.labelClear.event || {}"
                    :style="item.labelClear.style"
                    :class="item.labelClear.class"
                  >
                    Clear
                  </el-button>
                </div>
                <div class="custom-description">
                  <span class="label-text">{{ item.label || '' }}</span>
                  <span class="description" v-if="item.description">
                    {{ item.description }}
                  </span>
                </div>
              </template>
              <component
                v-for="(com, compIdx) in item.comp"
                :key="compIdx + com.comp + com.key"
                :is="com.comp"
                :ref="com.ref || ''"
                :size="com.size || size"
                :style="com.style"
                :class="com.class"
                v-model="formData[com.key]"
                v-bind="com.bind"
                v-on="com.event"
                @keyup.enter.native="handleCommonEnterEvent(com.comp, com.key, formData[com.key])"
                @blur="handleCommonBlurEvent(com.comp, com.key, formData[com.key])"
              >
                <!-- 组件内的插槽 -->
                <template v-if="com.comp === 'el-select'">
                  <el-option
                    v-for="(option, idx) in com.options"
                    :key="idx + '' + option.value"
                    :label="option.label"
                    :value="option.value"
                  >
                  </el-option>
                </template>
                <template v-if="com.slots && com.slots.length">
                  <component
                    v-for="(slot, slotIdx) in com.slots"
                    :key="slot.name + slot.comp + slotIdx"
                    :is="slot.comp"
                    :slot="slot.name"
                    :class="slot.class"
                    :style="slot.style"
                    v-bind="slot.bind"
                    v-on="slot.event"
                  >
                  </component>
                </template>
              </component>
            </el-form-item>
            <!-- 所有组件平分24 grid -->
            <el-row v-else :gutter="item.gutter || 20" type="flex" style="align-items: flex-end">
              <el-col
                v-for="(col, compIdx) in item.comp"
                :key="compIdx + col.comp"
                :span="24 / (item.comp.length || 1)"
              >
                <el-form-item :label="col.label || ''" :prop="col.prop || col.key" :class="col.formItemClass || ''">
                  <!-- 表单自定义label -->
                  <template v-slot:label>
                    <div class="custom-label" v-if="col.labelClear">
                      <span class="label-text">{{ col.label || '' }}</span>
                      <el-button
                        class="label-clear"
                        type="text"
                        size="small"
                        v-bind="col.labelClear.bind || {}"
                        v-on="col.labelClear.event || {}"
                        :style="col.labelClear.style"
                        :class="col.labelClear.class"
                      >
                        Clear
                      </el-button>
                    </div>
                  </template>
                  <component
                    :is="col.comp"
                    :key="col.comp + col.key"
                    :ref="col.ref || ''"
                    :size="col.size || size"
                    :style="col.style"
                    :class="col.class"
                    v-model="formData[col.key]"
                    v-bind="col.bind"
                    v-on="col.event"
                    @keyup.enter.native="handleCommonEnterEvent(col.comp, col.key, formData[col.key])"
                    @blur="handleCommonBlurEvent(col.comp, col.key, formData[col.key])"
                  >
                    <!-- 组件内的插槽 -->
                    <template v-if="col.comp === 'el-select'">
                      <el-option
                        v-for="(option, idx) in col.options"
                        :key="idx + '' + option.value"
                        :label="option.label"
                        :value="option.value"
                      >
                      </el-option>
                    </template>
                    <template v-if="col.slots && col.slots.length">
                      <component
                        v-for="(slot, slotIdx) in col.slots"
                        :key="slot.name + slot.comp + slotIdx"
                        :is="slot.comp"
                        :slot="slot.name"
                        :class="slot.class"
                        :style="slot.style"
                        v-bind="slot.bind"
                        v-on="slot.event"
                      >
                      </component>
                    </template>
                  </component>
                </el-form-item>
              </el-col>
            </el-row>
          </template>
        </el-col>
      </template>
      <!-- 表单为操作按钮预留了位置 -->
      <el-col
        v-if="needOperateBtn"
        class="col-flex-button-list"
        :xs="colSmall"
        :sm="colSmall"
        :md="colSmall"
        :lg="colSmall"
        :xl="colXL"
      >
        <div class="form-btns">
          <template v-if="!customOperateBtn">
            <el-button size="small" plain @click="handleResetClick"> Reset </el-button>
            <el-button
              plain
              size="small"
              type="primary"
              :disabled="searchBtnDisabled"
              :loading="searchBtnLoading"
              @click="handleSearchClick"
              v-preventReClick
            >
              Search
            </el-button>
          </template>
          <!-- 不同业务可在插槽处出入自己的按钮 -->
          <slot name="btn-list"></slot>
        </div>
      </el-col>
    </el-row>
    <div v-if="needBottomLine" class="form-flex-bottom-line"></div>
  </el-form>
</template>

<script>
export default {
  name: 'PortalForm',
  props: {
    formData: {
      // 参考el-form
      required: true,
      type: Object,
      default: () => ({}),
    },
    formRules: {
      // 表单校验规则，参考el-form
      type: Object,
      default: () => ({}),
    },
    formConfigList: {
      // 表单项的配置，用来配置el-form-item 和 内部组件的配置
      required: true,
      type: Array,
      default: () => [],
    },
    labelPosition: {
      // 参考el-form
      type: String,
      default: 'top',
    },
    size: {
      // form内部组件统一大小，参考el-form
      type: String,
      default: 'small',
    },
    rowGutter: {
      // 间距， 参考el-row
      type: Number,
      default: 20,
    },
    smallAutoColumn: {
      // 自适应列参数， 参考el-col
      type: Number,
      default: 3,
      validate: (value) => {
        return !(24 % value);
      },
    },
    bigAutoColumn: {
      // 自适应列参数， 参考el-col
      type: Number,
      default: 4,
      validate: (value) => {
        return !(24 % value);
      },
    },
    selectTriggerEnter: {
      // select组件是否响应enter事件
      type: Boolean,
      default: false,
    },
    customOperateBtn: {
      // 是否自定义表单操作按钮
      type: Boolean,
      default: false,
    },
    needOperateBtn: {
      // 是否需要操作按钮
      type: Boolean,
      default: true,
    },
    searchBtnDisabled: {
      // 搜索按钮不可用
      type: Boolean,
      default: false,
    },
    searchBtnLoading: {
      // 搜索按钮loading状态
      type: Boolean,
      default: false,
    },
    needBottomLine: {
      // 需要展示底边
      type: Boolean,
      default: true,
    },
    cunstomFormClass: {
      // 自定义表单class
      type: String,
      default: '',
    },
    formLineStyle: {
      // form行内样式
      type: String,
      default: '',
    },
  },
  data() {
    return {};
  },
  computed: {
    colXL() {
      return 24 / this.bigAutoColumn;
    },
    colSmall() {
      return 24 / this.smallAutoColumn;
    },
    innerFormRef() {
      // 组件内部的表单ref，外部验证时需要用到
      return this.$refs.portalForm || null;
    },
  },
  methods: {
    // 处理el-input的enter键盘事件，
    // 带有事件修饰符的，只能公共处理，不能通过对象传入进来
    handleCommonEnterEvent(comp, key, value) {
      if (comp !== 'el-select' || this.selectTriggerEnter) {
        this.$emit('common-enter-event', key, value);
      }
    },
    handleCommonBlurEvent(comp, key, value) {
      if (comp === 'el-input') {
        this.formData[key] = value.replace(/^\s+|\s+$/g, '');
      }
    },
    handleResetClick() {
      this.$emit('reset-btn-click');
      this.resetFields();
    },
    handleSearchClick() {
      this.innerFormRef &&
        this.innerFormRef.validate((valid) => {
          if (valid) {
            this.$emit('search-btn-click', this.formData);
          }
        });
    },
    // 重置内置表单
    resetFields() {
      this.innerFormRef && this.innerFormRef.resetFields();
    },
    // 重置内置表单的验证结果
    clearValidate(props) {
      this.innerFormRef && this.innerFormRef.clearValidate(props ? props : undefined);
    },
    // 对表单进行验证
    validate(callback) {
      return this.innerFormRef && this.innerFormRef.validate(callback);
    },
    validateField(field, callback) {
      return this.innerFormRef && this.innerFormRef.validateField(field, callback);
    },
  },
};
</script>

